.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.container-s {
  height: 100%;
}

.is-foot{
  position: fixed;
  bottom: 5%;
}
.mid-cont{
  padding-top: 2%;
}

.rowrisk{
  color: #e90a0a;
  /* background-color:#FF9C9C; */
}
.rowsymp{
  color: #770671;
  /* background-color: #d4d439; */

}
.rowexpose{
  color: #005c5c;
  /* background-color: #00fcfc; */
}

@media print {
  .printbutton {
    display: none;
  }
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
